<template>
  <div class="index" v-loading.fullscreen.lock="fullscreenLoading" element-loading-text="拼命加载中...">
    <input
      type="file"
      @change="importFile(this)"
      id="imFile"
      style="display: none"
      accept="application/vnd.openxmlformats-officedocument.spreadsheetml.sheet, application/vnd.ms-excel"
    />
    <a id="downlink"></a>
    <el-button class="button" @click="uploadFile()">导入</el-button>
    <el-button class="button" @click="downloadFile(excelData)">导出</el-button>
    <span>导入要有固定格式，格式参照导出，且表头禁止修改</span>
    <!--错误信息提示-->
    <el-dialog title="提示" v-model="errorDialog" size="tiny">
      <span>{{errorMsg}}</span>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="errorDialog=false">确认</el-button>
      </span>
    </el-dialog>
    <!--展示导入信息-->
    <el-table :data="excelData" tooltip-effect="dark" class="tabl">
      <el-table-column label="姓名" prop="xm"></el-table-column>
      <el-table-column label="性别" prop="xb"></el-table-column>
      <el-table-column label="出生年月" prop="csny"></el-table-column>
      <el-table-column label="身份证号" prop="sfzh"></el-table-column>
      <el-table-column label="手机号码" prop="sjhm"></el-table-column>
    </el-table>
  </div>
</template>

<script>
// 引入xlsx
var XLSX = require("xlsx");
export default {
  name: "Index",
  data() {
    return {
      fullscreenLoading: false, // 加载中
      imFile: "", // 导入文件el
      outFile: "", // 导出文件el
      errorDialog: false, // 错误信息弹窗
      errorMsg: "", // 错误信息内容
      excelData: [
        // 测试数据
        {
          xm: "张三",
          xb: "男",
          csny: "1980-02",
          sfzh: "330522198002011102",
          sjhm: "13111112222"
        },
        {
          xm: "李四",
          xb: "女",
          csny: "1963-02",
          sfzh: "330522198002011102",
          sjhm: "13111112222"
        }
      ],
      tabH: [
        {
          xm: "姓名",
          xb: "性别",
          csny: "出生年月",
          sfzh: "身份证号",
          sjhm: "手机号码",
          xj: "星级",
          stdx: "十条底线",
          xfzs: "先锋指数",
          pjjg: "评价结果"
        }
      ]
    };
  },
  mounted() {
    this.imFile = document.getElementById("imFile");
    this.outFile = document.getElementById("downlink");
  },
  methods: {
    uploadFile: function() {
      // 点击导入按钮
      this.imFile.click();
    },
    downloadFile: function(rs) {
      // 点击导出按钮
      let ths = this.tabH[0];
      let data = [];
      data = data.concat(ths);
      data = data.concat(rs);
      console.log(data);
      //  以时间命名excel标题
      let date = new Date();
      var year = date.getFullYear();
      var month = date.getMonth() + 1;
      var day = date.getDate();
      this.downloadExl(data, year + "-" + month + "-" + day);
    },
    importFile: function() {
      // 导入excel
      this.fullscreenLoading = true;
      let obj = this.imFile;
      if (!obj.files) {
        this.fullscreenLoading = false;
        return;
      }
      var f = obj.files[0];
      var reader = new FileReader();
      let $t = this;
      reader.onload = function(e) {
        var data = e.target.result;
        if ($t.rABS) {
          $t.wb = XLSX.read(btoa(this.fixdata(data)), {
            // 手动转化
            type: "base64"
          });
        } else {
          $t.wb = XLSX.read(data, {
            type: "binary"
          });
        }
        let json = XLSX.utils.sheet_to_json($t.wb.Sheets[$t.wb.SheetNames[0]]);
        console.log(typeof json);
        $t.dealFile($t.analyzeData(json)); // analyzeData: 解析导入数据
      };
      if (this.rABS) {
        reader.readAsArrayBuffer(f);
      } else {
        reader.readAsBinaryString(f);
      }
    },
    downloadExl: function(json, downName, type) {
      // 导出到excel
      let keyMap = []; // 获取键
      for (let k in json[0]) {
        keyMap.push(k);
      }
      console.info("keyMap", keyMap, json);
      let tmpdata = []; // 用来保存转换好的json
      json
        .map((v, i) =>
          keyMap.map((k, j) =>
            Object.assign(
              {},
              {
                v: v[k],
                position:
                  (j > 25 ? this.getCharCol(j) : String.fromCharCode(65 + j)) +
                  (i + 1)
              }
            )
          )
        )
        .reduce((prev, next) => prev.concat(next))
        .forEach(function(v) {
          tmpdata[v.position] = {
            v: v.v
          };
        });
      let outputPos = Object.keys(tmpdata); // 设置区域,比如表格从A1到D10
      let tmpWB = {
        SheetNames: ["mySheet"], // 保存的表标题
        Sheets: {
          mySheet: Object.assign(
            {},
            tmpdata, // 内容
            {
              "!ref": outputPos[0] + ":" + outputPos[outputPos.length - 1] // 设置填充区域
            }
          )
        }
      };
      let tmpDown = new Blob(
        [
          this.s2ab(
            XLSX.write(
              tmpWB,
              {
                bookType: type === undefined ? "xlsx" : type,
                bookSST: false,
                type: "binary"
              } // 这里的数据是用来定义导出的格式类型
            )
          )
        ],
        {
          type: ""
        }
      ); // 创建二进制对象写入转换好的字节流
      var href = URL.createObjectURL(tmpDown); // 创建对象超链接
      this.outFile.download = downName + ".xlsx"; // 下载名称
      this.outFile.href = href; // 绑定a标签
      this.outFile.click(); // 模拟点击实现下载
      setTimeout(function() {
        // 延时释放
        URL.revokeObjectURL(tmpDown); // 用URL.revokeObjectURL()来释放这个object URL
      }, 100);
    },
    analyzeData: function(data) {
      // 此处可以解析导入数据
      return data;
    },
    dealFile: function(data) {
      // 处理导入的数据
      console.log(data);
      let th = JSON.parse(JSON.stringify(this.tabH[0]));
      //  转换对象的key和value
      for (let k in th) {
        let value = th[k]; //将原来的value赋值给一个变量
        th[value] = k; // 为cluster_info赋新key，不能直接使用cluster_info = {cluster_info[k] : k},会报语法错误
        delete th[k]; // 删除原来的属性
      }
      //  把data中的key替换为prop，表格的键
      for (let i = 0; i < data.length; i++) {
        for (let key in data[i]) {
          let newKey = th[key];
          if (newKey) {
            data[i][newKey] = data[i][key];
            delete data[i][key];
          }
        }
      }
      console.log(data);
      this.imFile.value = "";
      this.fullscreenLoading = false;
      if (data.length <= 0) {
        this.errorDialog = true;
        this.errorMsg = "请导入正确信息";
      } else {
        //  将格式化后的导入数据与原数据合并
        this.excelData = this.excelData.concat(data);
      }
    },
    s2ab: function(s) {
      // 字符串转字符流
      var buf = new ArrayBuffer(s.length);
      var view = new Uint8Array(buf);
      for (var i = 0; i !== s.length; ++i) {
        view[i] = s.charCodeAt(i) & 0xff;
      }
      return buf;
    },
    getCharCol: function(n) {
      // 将指定的自然数转换为26进制表示。映射关系：[0-25] -> [A-Z]。
      let s = "";
      let m = 0;
      while (n > 0) {
        m = (n % 26) + 1;
        s = String.fromCharCode(m + 64) + s;
        n = (n - m) / 26;
      }
      return s;
    },
    fixdata: function(data) {
      // 文件流转BinaryString
      var o = "";
      var l = 0;
      var w = 10240;
      for (; l < data.byteLength / w; ++l) {
        o += String.fromCharCode.apply(
          null,
          new Uint8Array(data.slice(l * w, l * w + w))
        );
      }
      o += String.fromCharCode.apply(null, new Uint8Array(data.slice(l * w)));
      return o;
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.el-table th > .cell {
  text-align: center;
}
.button {
  margin-bottom: 20px;
}
.tabl tr td {
  text-align: center;
}
</style>



